.auth {
  width: 100%;
  max-width: 935px;
  margin: 0 auto;
  display: flex;
  min-height: 100%;
  margin-top: 35px;
  justify-content: center;
  align-items: center; }
  @media (min-width: 320px) and (max-width: 550px) {
    .auth {
      margin-top: 10px; } }
  .auth .column {
    width: 40%; }
    @media (min-width: 320px) and (max-width: 550px) {
      .auth .column:first-child {
        display: none; } }
    @media (min-width: 550px) and (max-width: 875px) {
      .auth .column:first-child {
        display: none; } }
    .auth .column:last-child {
      width: 38%;
      margin-left: 35px; }
      @media (min-width: 320px) and (max-width: 550px) {
        .auth .column:last-child {
          width: 100%;
          margin: 0; } }
      @media (min-width: 550px) and (max-width: 875px) {
        .auth .column:last-child {
          width: 70%;
          max-width: 350px;
          margin: 0; } }
    .auth .column .app-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 35px; }
      .auth .column .app-box .appstores {
        margin-top: 25px; }
        .auth .column .app-box .appstores img {
          height: 40px; }
          .auth .column .app-box .appstores img:first-child {
            margin-right: 10px; }
    .auth .column .white-box {
      text-align: center;
      padding: 15px;
      border: 1px solid #e6e6e6;
      background-color: white; }
      @media (min-width: 320px) and (max-width: 550px) {
        .auth .column .white-box {
          background-color: transparent;
          border: 0; } }
      .auth .column .white-box .change-link {
        color: #3897f0;
        cursor: pointer; }
      .auth .column .white-box .text {
        margin: 15px; }
    .auth .column .form-box {
      margin-bottom: 10px;
      padding: 0 40px;
      padding-top: 30px; }
      .auth .column .form-box img {
        height: 60px; }
