.feed-photo {
  border: 1px solid #e6e6e6;
  background-color: white;
  border-radius: 3px;
  width: 100%;
  max-width: 600px;
  margin-bottom: 60px; }
  @media (min-width: 320px) and (max-width: 550px) {
    .feed-photo {
      background-color: transparent;
      border: 0;
      margin-bottom: 30px; } }
  .feed-photo:last-child {
    margin-bottom: 0; }
  .feed-photo .header {
    padding: 15px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e6e6e6; }
    .feed-photo .header .image {
      width: 30px;
      border-radius: 50%;
      margin-right: 15px; }
    .feed-photo .header .header-column {
      display: flex;
      flex-direction: column; }
      .feed-photo .header .header-column .creator {
        font-weight: 600;
        margin-bottom: 5px; }
      .feed-photo .header .header-column .location {
        font-size: 12px; }
  .feed-photo .meta {
    padding: 15px;
    padding-bottom: 0;
    padding-top: 10px; }
