.footer {
  max-width: 935px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 12px;
  margin: 55px auto;
  flex-wrap: wrap; }
  @media (max-width: 875px) {
    .footer {
      justify-content: center;
      max-width: 350px;
      margin-bottom: 25px; } }
  @media (max-width: 875px) {
    .footer .column {
      margin-bottom: 0; } }
  .footer .column .nav .list {
    display: flex;
    flex-wrap: wrap; }
    @media (max-width: 875px) {
      .footer .column .nav .list {
        justify-content: center; } }
    .footer .column .nav .list .list-item {
      margin-right: 15px;
      color: #003569;
      cursor: pointer; }
      @media (max-width: 875px) {
        .footer .column .nav .list .list-item {
          margin-bottom: 10px;
          margin-right: 15px; } }
  .footer .column .copyright {
    color: #999; }
