.form-component {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px; }
  .form-component .form {
    display: flex;
    flex-direction: column; }
    .form-component .form input[type="submit"] {
      margin-top: 10px; }
    .form-component .form .text-input {
      border: 1px solid #efefef;
      border-radius: 3px;
      background-color: #fafafa;
      margin-bottom: 7px;
      font-size: 14px;
      padding: 9px 0 7px 8px;
      font-weight: 400; }
      .form-component .form .text-input:focus {
        outline: none;
        border: 1px solid #b2b2b2; }
  .form-component .button {
    -webkit-appearance: none;
    border-radius: 3px;
    background-color: #3897f0;
    color: white;
    font-weight: 600;
    border: 0;
    font-size: 14px;
    line-height: 26px;
    cursor: pointer;
    width: 100%;
    padding: 2px 9px; }
    .form-component .button:focus, .form-component .button:active {
      outline: none; }
    .form-component .button:active {
      opacity: 0.7; }
    .form-component .button i {
      font-size: 18px;
      margin-right: 5px; }
    .form-component .button svg {
      margin-right: 10px; }
  .form-component .divider {
    text-transform: uppercase;
    color: #999;
    font-weight: 600;
    margin: 20px 0;
    margin-bottom: 20px;
    position: relative; }
    .form-component .divider:before {
      content: "";
      width: 42%;
      position: absolute;
      background-color: #999;
      height: 1px;
      left: 0;
      top: 6px;
      opacity: 0.8; }
    .form-component .divider:after {
      content: "";
      width: 42%;
      position: absolute;
      background-color: #999;
      height: 1px;
      right: 0;
      top: 6px;
      opacity: 0.8; }
  .form-component .facebook-link {
    align-items: center;
    color: #385185;
    justify-content: center;
    margin-bottom: 25px;
    cursor: pointer;
    background-color: white;
    border: 0;
    font-size: 14px;
    text-align: center; }
    .form-component .facebook-link i {
      margin-right: 5px; }
    .form-component .facebook-link:focus, .form-component .facebook-link:active {
      outline: none; }
  .form-component .forgot-link {
    font-size: 12px;
    color: #003569; }
  .form-component .signup-header {
    font-size: 17px;
    color: #999;
    font-weight: 600;
    margin-bottom: 20px; }
  .form-component .terms {
    width: 80%;
    margin: 0 auto;
    margin-top: 20px;
    line-height: 20px;
    color: #999; }
    .form-component .terms span {
      font-weight: 600;
      cursor: pointer; }
