.navigation {
  width: 100%;
  border: 1px solid #e6e6e6;
  background-color: white;
  display: flex;
  justify-content: center;
  margin-bottom: 60px;
  box-sizing: border-box; }
  @media (min-width: 320px) and (max-width: 550px) {
    .navigation {
      margin-bottom: 0; } }
  .navigation .inner {
    max-width: 1015px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 77px;
    padding: 25px 40px; }
    .navigation .inner .column {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 33%; }
      @media (min-width: 320px) and (max-width: 550px) {
        .navigation .inner .column:nth-child(2) {
          display: none; } }
      .navigation .inner .column:first-child {
        justify-content: flex-start; }
      .navigation .inner .column:last-child {
        justify-content: flex-end; }
      .navigation .inner .column .logo {
        max-width: 125px; }
      .navigation .inner .column .search-input {
        max-width: 215px;
        width: 100%;
        box-sizing: border-box;
        padding: 5px;
        border: solid 1px #dbdbdb;
        border-radius: 3px;
        background-color: #fafafa;
        font-size: 14px;
        font-weight: 300;
        margin-right: 15px; }
        .navigation .inner .column .search-input::placeholder {
          color: #999; }
        .navigation .inner .column .search-input:focus {
          outline: none;
          background-color: white; }
      .navigation .inner .column .nav-icon {
        margin-left: 30px;
        cursor: pointer; }
