.container {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .container .box {
    border: 1px solid #e6e6e6;
    background-color: white;
    width: 100%;
    max-width: 600px; }
    .container .box .header {
      display: flex;
      padding: 14px 0;
      justify-content: center;
      align-items: center;
      border-bottom: 2px solid #efefef; }
      .container .box .header svg {
        cursor: pointer; }
      .container .box .header .title {
        order: 1;
        margin-left: auto;
        margin-right: auto;
        font-weight: 600;
        font-size: 16px; }
    .container .box .content {
      min-height: 300px;
      display: flex;
      align-items: flex-start;
      flex-direction: column; }
