.comment-box {
  border-top: 1px solid #efefef;
  padding: 15px 0; }
  @media (min-width: 320px) and (max-width: 550px) {
    .comment-box {
      display: none; } }
  .comment-box .input {
    border: 0;
    height: 20px;
    resize: none;
    width: 100%;
    font-size: 14px; }
    .comment-box .input:focus {
      outline: none; }
    .comment-box .input::placeholder {
      color: #999; }
