.search {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 935px;
  margin: 0 auto;
  align-items: center; }
  .search .section {
    width: 100%;
    margin-bottom: 80px; }
    .search .section .title {
      text-align: left;
      color: #999;
      font-weight: 600;
      margin-bottom: 20px; }
    .search .section .not-found {
      text-align: center;
      width: 100%;
      display: block;
      font-weight: 600; }
    .search .section .content {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap; }
