.container {
  width: 30%;
  height: 220px;
  margin-bottom: 50px;
  overflow: hidden;
  position: relative; }
  .container .photo {
    min-height: 100%;
    max-width: 100%; }
  .container:hover .overlay {
    opacity: 1; }
  .container .overlay {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0; }
    .container .overlay .data {
      color: white;
      font-weight: 600;
      display: flex;
      align-items: center; }
      .container .overlay .data:first-child {
        margin-right: 10px; }
      .container .overlay .data svg {
        margin-right: 5px; }
