.horizontal,
.vertical {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid #efefef; }
  @media (max-width: 875px) {
    .horizontal,
    .vertical {
      border-bottom: 0; } }
  .horizontal .column,
  .vertical .column {
    display: flex;
    align-items: center; }
    .horizontal .column .user,
    .vertical .column .user {
      margin-left: 10px; }
      .horizontal .column .user .username,
      .vertical .column .user .username {
        font-weight: 600;
        display: block;
        margin-bottom: 3px; }
      .horizontal .column .user .name,
      .vertical .column .user .name {
        color: #999; }
    .horizontal .column .bigAvatar,
    .vertical .column .bigAvatar {
      width: 55px;
      border-radius: 50%; }
    .horizontal .column .avatar,
    .vertical .column .avatar {
      width: 30px;
      border-radius: 50%; }
    .horizontal .column .button,
    .vertical .column .button {
      -webkit-appearance: none;
      border-radius: 3px;
      background-color: #3897f0;
      color: white;
      font-weight: 600;
      border: 0;
      font-size: 14px;
      line-height: 26px;
      cursor: pointer;
      width: 100%;
      padding: 2px 9px; }
      .horizontal .column .button:focus, .horizontal .column .button:active,
      .vertical .column .button:focus,
      .vertical .column .button:active {
        outline: none; }
      .horizontal .column .button:active,
      .vertical .column .button:active {
        opacity: 0.7; }

.horizontal:last-child {
  border: 0; }

.vertical {
  flex-direction: column;
  width: 30%;
  justify-content: center;
  align-items: center;
  height: 220px;
  border: 1px solid #e6e6e6;
  background-color: white;
  border-radius: 3px; }
  .vertical .column {
    flex-direction: column; }
    .vertical .column .avatar {
      width: 78px;
      margin-bottom: 20px; }
    .vertical .column .user {
      text-align: center;
      margin: 0; }
      .vertical .column .user .username {
        margin-bottom: 7px; }
  .vertical .button {
    margin-top: 15px;
    min-width: 95px; }
